	module.exports = {"data":{"markdownRemark":{"html":"<p>Narayan Sarovar lake is one of the key highlights of the Kutch district owing to its natural beauty as well as its huge religious significance. Narayan Sarovar Lake is one of the 5 holy lakes of Hinduism, along with Mansarovar in Tibet, Pampa in Karnataka, Bhuvaneshwar in Orissa and Pushkar in Rajasthan.</p>\n<p>The lake is associated with a time of drought in the Puranic area, when Narayan (a form of Lord Vishnu) appeared in response to the fervent prayers of sages and touched the land with his toe, creating the lake. The area surrounding the lake is also rich with flora &#x26; fauna &#x26; one can often see ‘Chinkara’, a form of antelope nearby.</p>\n<p>Narayan Sarovar lake is one of the key highlights of the Kutch district owing to its natural beauty as well as its huge religious significance. Narayan Sarovar Lake is one of the 5 holy lakes of Hinduism, along with Mansarovar in Tibet, Pampa in Karnataka, Bhuvaneshwar in Orissa and Pushkar in Rajasthan.</p>\n<p>The lake is associated with a time of drought in the Puranic area, when Narayan (a form of Lord Vishnu) appeared in response to the fervent prayers of sages and touched the land with his toe, creating the lake. The area surrounding the lake is also rich with flora &#x26; fauna &#x26; one can often see ‘Chinkara’, a form of antelope nearby.</p>","frontmatter":{"title":"Narayan Sarovar"}}},"pathContext":{"slug":"/narayansarovar/"}}

